*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  transition-delay: 1s;
  height: 100%;
}

body {
  font-family: "Manrope", sans-serif;
  background-color: rgb(255, 255, 255);
  overflow-x: hidden;
  height: 100%;
}

main {
  position: relative;
  width: 100%;
}

.privacypolicy {
  padding: 0 2rem;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgb(255, 255, 255);
  transition: transform 0.4s;
  z-index: 10;
  overflow: scroll;
}
.privacypolicy__head {
  margin: 10rem auto 0;
  display: flex;
  justify-content: flex-end;
  max-width: 900px;
}
.privacypolicy__head-logo {
  max-width: 200px;
  height: 100%;
}
.privacypolicy__head-logo img {
  aspect-ratio: 11/4;
  max-width: 150px;
  width: 100%;
  height: auto;
}
.privacypolicy__head-btns button {
  background-color: transparent;
  color: rgb(0, 0, 0);
  transition: color 0.4s;
}
.privacypolicy__head-btns button:hover {
  color: rgb(196, 46, 56);
}
.privacypolicy__close-btn {
  position: absolute;
  top: 2.7rem;
  right: 4rem;
  padding: 1rem;
  background-color: transparent;
  color: rgb(0, 0, 0);
  border: none;
  cursor: pointer;
}
.privacypolicy__close-btn svg {
  width: 28px;
  height: 28px;
}
.privacypolicy__body {
  margin: 0 auto;
  padding-bottom: 4rem;
  max-width: 900px;
  width: 100%;
  font-family: "Roboto Mono", monospace;
}
.privacypolicy__body h2 {
  margin-bottom: 2rem;
  font-size: 2.6rem;
  text-transform: uppercase;
}
.privacypolicy__body h3 {
  margin-top: 1.2rem;
  font-size: 2rem;
}
.privacypolicy__body li,
.privacypolicy__body span {
  font-size: 1.2rem;
}
.privacypolicy__body li {
  list-style: none;
}
.privacypolicy__main-ul {
  margin-left: 2rem;
}
.privacypolicy__submain-ul {
  margin-left: 3.6rem;
}
.privacypolicy__submain-ul li {
  list-style: disc;
}

@media (max-width: 992px) {
  .privacypolicy__head {
    margin: 2rem auto 4rem;
    justify-content: flex-start;
  }
}
@media (max-width: 565px) {
  .privacypolicy__head-logo img {
    max-width: 100px;
  }
}
@media (max-width: 480px) {
  .privacypolicy__body h2 {
    font-size: 2.2rem;
  }
  .privacypolicy__body h3 {
    font-size: 1.8rem;
  }
  .privacypolicy__main-ul {
    margin-left: 0;
  }
}